import React from 'react'
import { Box, Btn } from '../UI'
import { extratoLista }  from '../../info'
import Item from '../Items'

const Extrato = () => {
  return (
    <Box>
        {extratoLista.updates.map(({id, type, from, value, date}) => {
            return(
                <Item 
                key={id} 
                type={type} 
                from={from} 
                value={value} 
                date={date} />
            )
        })}
        <Btn>Ver mais</Btn>
    </Box>
  )
}

export default Extrato