import React from "react"
import alimentacao from "../assets/images/alimentacao.svg"
import outros from "../assets/images/outros.svg"
import saude from "../assets/images/saude.svg"
import transporte from "../assets/images/transporte.svg"
import utilidades from "../assets/images/utilidades.svg"
import { IconLightdark } from "./UI"

export default (type) => {
    const Images = {
        Restaurante: <IconLightdark src={alimentacao}/>,
        Utilidades: <IconLightdark src={utilidades}/>,
        Saude: <IconLightdark src={saude}/>,
        Transporte: <IconLightdark src={transporte}/>,
        default: <IconLightdark src={outros}/>,
    }

    return Images[type] || Images.default;
}